@font-face {
  font-display: swap;
  font-family: "ProximaNova";
  src: url("../fonts/ProximaNova/normal/regular.eot");
  src: url("../fonts/ProximaNova/normal/regular.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova/normal/regular.woff2") format("woff2"), url("../fonts/ProximaNova/normal/regular.woff") format("woff"), url("../fonts/ProximaNova/normal/regular.ttf") format("truetype"), url("../fonts/ProximaNova/normal/regular.svg#ProximaNova") format("svg");
  font-style: normal;
  font-weight: 400;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body,
div,
table,
tr,
td,
h1,
h2,
h3,
li {
  position: relative;
}

body {
  font-family: Roboto, arial;
  font-size: 14px;
  font-weight: 400;
  color: #4d4d4d;
  line-height: 22px;
  min-width: 320px;
  margin: 0;
}

html,
body {
  height: 100%;
}

blockquote,
q {
  quotes: none;
}

.clear {
  clear: both;
}

hr {
  width: 100%;
  height: 1px;
  margin: 40px 0;
  border: none;
  background-color: #ededee;
}

a {
  color: #0072bb;
  text-decoration: none;
  border-bottom: 1px solid #cce3f1;
  outline: none;
}

a:hover {
  color: #009ee2;
  text-decoration: none;
  border-color: transparent;
}

a:focus {
  text-decoration: none;
  outline: none;
  border-color: transparent;
}

a:active {
  outline: none;
}

h1 {
  font-size: 30px;
  margin: 0;
  line-height: 34px;
}

h2 {
  font-size: 22px;
  margin: 40px 0 20px;
  line-height: 26px;
}

h3 {
  font-size: 16px;
  margin: 20px 0 15px;
  line-height: 24px;
}

p {
  margin: 0 0 15px;
}

ul {
  margin: 0 0 15px;
}

ul li {
  list-style: none;
}

ul li:before {
  content: "\2022";
  position: absolute;
  top: 0;
  left: -10px;
}

ol {
  margin: 0 0 15px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

table {
  width: 100%;
  margin: 0 0 15px;
  border: none;
  border-collapse: collapse;
  border-spacing: 0;
}

table tbody {
  border: none;
}

table tr.odd,
table tr.even {
  background-color: transparent;
}

table td,
table th {
  border: none;
  padding: 0 14px 0;
  line-height: 36px;
}

table.nostyle {
  border: none;
}

table.nostyle thead {
  background-color: transparent;
}

table.nostyle tr {
  background-color: transparent;
}

table.nostyle tr th,
table.nostyle tr td {
  border: none;
  padding: 0;
  line-height: inherit !important;
}

.container {
  width: 1140px;
  margin: 0 auto;
}

.table-wrapper {
  overflow: auto;
}

a.lightgallery {
  border-bottom: none;
}

ul.slick-dots li button,
.slick-slide,
.slick-arrow {
  outline: none;
}

.margin {
  margin: 0 0 70px;
}

.margin-small {
  margin: 0 0 50px;
}

.padding {
  padding: 70px 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
}

.grey {
  color: #818181;
}

.link-wimg {
  position: relative;
  padding-left: 60px;
  border-color: transparent;
}

.telegram {
  font-family: 'Open Sans Condensed', sans-serif;
  color: #818181;
  font-size: 30px;
}

.telegram::before {
  position: absolute;
  content: '';
  top: 50%;
  left: 0;
  width: 45px;
  height: 45px;
  transform: translate3d(0, -50%, 0);
  background: url(../img/icons-svg/telegram.svg);
  background-repeat: no-repeat;
  background-size: 45px 45px;
}

.mail {
  font-family: 'Open Sans Condensed', sans-serif;
  color: #818181;
  font-size: 30px;
}

.mail::before {
  position: absolute;
  content: '';
  top: 50%;
  left: 0;
  width: 45px;
  height: 45px;
  transform: translate3d(0, -50%, 0);
  background: url(../img/icons-svg/mail.svg);
  background-repeat: no-repeat;
  background-size: 45px 45px;
}

.bb {
  border: 3px solid #101010;
  position: relative;
  padding: 40px;
}

button {
  outline: 0;
}

button:focus {
  outline: none;
}

.btn {
  cursor: pointer;
  text-align: center;
  font-weight: 600;
  display: inline-block;
  padding: 6px 14px 7px;
  position: relative;
}

.btn_default {
  border-width: 1px;
  border-style: solid;
  transition: all 0.25s ease-in-out;
  text-decoration: none;
}

.btn_default:hover {
  text-decoration: none;
}

.btn_default:focus {
  text-decoration: none;
}

.btn_primary {
  border-width: 1px;
  border-style: solid;
  transition: all 0.25s ease-in-out;
  text-decoration: none;
}

.btn_primary:hover {
  text-decoration: none;
}

.btn_primary:focus {
  text-decoration: none;
}

.btn_link {
  background: none;
  border: none;
  border-bottom: 1px solid;
  font-weight: 400;
  padding: 0;
}

.btn_download {
  border: none;
  font-weight: 600;
  position: relative;
  padding-left: 42px;
  padding-top: 8px;
}

.btn_download::before {
  content: "\f56d";
  font-family: 'Font Awesome 5 Pro';
  font-size: 37px;
  font-weight: 100;
  line-height: 1;
  position: absolute;
  position: absolute;
  top: -2px;
  left: 0;
}

.btn_download span {
  border-bottom: 1px solid;
}

.btn_upper {
  text-transform: uppercase;
}

.btn_block {
  display: block;
  width: 100%;
}

.btn_lg {
  padding: .5rem 1rem;
  font-size: 1.25rem;
}

.btn_sm {
  padding: .25rem .5rem;
  font-size: .875rem;
}

.btn_radius {
  border-radius: 4px;
}

.btn_icon-left {
  padding-left: 54px;
}

.btn_icon-left:before {
  position: absolute;
  top: 1px;
  bottom: 0;
  left: 18px;
  display: flex;
  align-items: center;
}

.btn_icon-right {
  padding-right: 54px;
}

.btn_icon-right:before {
  position: absolute;
  top: 1px;
  bottom: 0;
  right: 18px;
  display: flex;
  align-items: center;
}

input[type="text"],
input[type="password"],
input[type="email"],
textarea {
  width: 100%;
  padding: 9px 15px 11px;
  transition: all 0.3s ease-in-out;
  outline: none;
  font-size: 14px;
  box-shadow: none;
  border-radius: 7px;
  background-color: transparent;
}

input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
textarea:focus {
  outline: none;
}

input[type="text"]:focus::-webkit-input-placeholder,
input[type="password"]:focus::-webkit-input-placeholder,
input[type="email"]:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
  opacity: 0;
}

input[type="text"]:focus::-moz-placeholder,
input[type="password"]:focus::-moz-placeholder,
input[type="email"]:focus::-moz-placeholder,
textarea:focus::-moz-placeholder {
  opacity: 0;
}

input[type="text"]:focus:-moz-placeholder,
input[type="password"]:focus:-moz-placeholder,
input[type="email"]:focus:-moz-placeholder,
textarea:focus:-moz-placeholder {
  opacity: 0;
}

input[type="text"]:focus:-ms-input-placeholder,
input[type="password"]:focus:-ms-input-placeholder,
input[type="email"]:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder {
  opacity: 0;
}

textarea {
  resize: none;
}

input[type="submit"] {
  outline: none;
}

@media only screen and (max-width: 1199px) {
  .container {
    width: 970px;
  }
}

@media only screen and (max-width: 991px) {
  .table-m-column {
    display: block;
    margin-top: 20px;
    border: none;
  }

  .table-m-column tbody {
    display: block;
  }

  .table-m-column tr {
    display: block;
  }

  .table-m-column td,
  .table-m-column th {
    display: block;
    width: 100% !important;
    padding: 0;
  }

  .table-m-nobg tr {
    background-color: transparent !important;
  }

  .container {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}