.container {
    @include max-screen(1199px) {
        width: 970px;
    }
    @include max-screen(991px) {
        width: 100%;
        padding-left: 15px;
        padding-right: 15px;
    }
    // @include max-screen(767px) {
    //     width: 100%;
    //     padding-left: 15px;
    //     padding-right: 15px;
    // }
    width: 1140px;
    margin: 0 auto;
}
.noscroll {}
.table-wrapper {
    overflow: auto;
}
a.lightgallery {
    border-bottom: none;
}
ul.slick-dots li button, .slick-slide, .slick-arrow {
    outline: none;
}
.margin {
    margin: map-get($margin, block);
}
.margin-small {
    margin: map-get($margin, block2);
}
.padding {
    padding: $padding;
}

.wrapper {
    display: flex;
    flex-direction: column;
}

.grey {
    color: #818181;
}
.link-wimg {
    position: relative;
    padding-left: 60px;
    border-color: transparent;
}
.telegram {
    font-family: 'Open Sans Condensed', sans-serif;
    color: #818181;
    font-size: 30px;
    &::before {
        position: absolute;
        content: '';
        top: 50%;
        left: 0;
        width: 45px;
        height: 45px;
        transform: translate3d(0, -50%, 0);
        background: url(../img/icons-svg/telegram.svg);
        background-repeat: no-repeat;
        background-size: 45px 45px;
    }
}
.mail {
    font-family: 'Open Sans Condensed', sans-serif;
    color: #818181;
    font-size: 30px;

    &::before {
        position: absolute;
        content: '';
        top: 50%;
        left: 0;
        width: 45px;
        height: 45px;
        transform: translate3d(0, -50%, 0);
        background: url(../img/icons-svg/mail.svg);
        background-repeat: no-repeat;
        background-size: 45px 45px;
    }
}

.bb {
    border: 3px solid #101010;
    position: relative;
    padding: 40px;
}